import React from 'react';

import { Link, Redirect, Route, Switch } from 'react-router-dom';

import usePageViews from '../../hooks/usePageViews';
import Investments from '../../pages/Investments/List/Investments';
import NewInvestment from '../../pages/Investments/New/NewInvestment';
import Contracts from '../../pages/Contracts/Contracts';
import Dashboard from '../../pages/Dashboard/Dashboard';
import NotFoundPage from '../../pages/notfound';
import PrivateRoute from './PrivateRoute';

const AppRouterSwitch: React.FC = () => {
  usePageViews();
  return (
    <div>
      <Switch>
        <PrivateRoute exact path="/" component={Dashboard} />
        <PrivateRoute exact path="/investments" component={Investments} />
        <PrivateRoute exact path="/investments/new" component={NewInvestment} />
        <PrivateRoute exact path="/contracts" component={Contracts} />
        <Route path="/404" component={NotFoundPage} />
        <Redirect to="/404" />
      </Switch>
    </div>
  );
};

export default AppRouterSwitch;
