import { isDesktop, isTablet } from 'react-device-detect';


/** 
 * 
 * Recebe 3 parâmetros e retorna o que for relacionado ao dispositivo (desktop, tablet ou phone).
 * @param valueIfDesktop
 * @param valueIfTablet
 * @param valueIfMobile
*/
export const flexValue = (valueIfDesktop: any, valueIfTablet: any, valueIfMobile: any) => {
    return isDesktop ? valueIfDesktop : isTablet ? valueIfTablet : valueIfMobile
}