import React from 'react';
import { Container, Icon, Title, TitleContainer } from './Card.styles';

const Card = ({
  title,
  icon,
  children,
  style,
  className,
}: {
  title: any;
  children?: any;
  icon: any;
  style?: any;
  className?: any;
}) => {
  return (
    <Container style={style} className={className}>
      <TitleContainer>
        <Icon src={icon} /> <Title>{title}</Title>
      </TitleContainer>
      {children}
    </Container>
  );
};

export default Card;
