import React from 'react';
import {
  SideMenu,
  ItemText,
  Divider,
  ItemWrapper,
  ItemIcon,
  SelectedBar,
  StyledLink,
  ContactContainer,
} from './Menu.styles';
import UserHeadset from '../../assets/icons/user-headset.svg';
import { useHistory } from 'react-router-dom';
import { MenuData } from './Menu.data';

const MenuItem = (props: {
  icon: any;
  selected?: boolean;
  children: any;
  style?: any;
}) => {
  const { icon, selected, children, ...rest } = props;

  return (
    <ItemWrapper selected={props.selected} {...rest}>
      {props.selected && <SelectedBar />}
      {icon && <ItemIcon src={icon} />}
      <ItemText className={'p-d-md-none p-d-none p-d-lg-inline-block'}>{props.children}</ItemText>
    </ItemWrapper>
  );
};

const Menu: React.FC = () => {
  let history = useHistory();
  const pathname = history.location.pathname;

  return (
    <SideMenu>
      {MenuData.map((item, index) => (
        <>
          <StyledLink to={item.location}>
            <MenuItem icon={item.icon} selected={pathname === item.location}>
              {item.text}
            </MenuItem>
          </StyledLink>
          {index < MenuData.length - 1 && <Divider />}
        </>
      ))}

      <ContactContainer>
        <Divider />
        <MenuItem icon={UserHeadset}>Atendimento</MenuItem>
      </ContactContainer>
    </SideMenu>
  );
};

export default Menu;
