import React from 'react';
import {
  Container,
  Icon,
  IconContainer,
  TextContainer,
  Title as TitleDiv,
  Subheading,
} from './Title.styles';

const Title = ({
  title,
  subheading,
  icon,
}: {
  title: any;
  subheading: any;
  icon: any;
}) => {
  return (
    <Container>
      <IconContainer>
        <Icon src={icon} />
      </IconContainer>
      <TextContainer>
        <TitleDiv>{title}</TitleDiv>
        <Subheading>{subheading}</Subheading>
      </TextContainer>
    </Container>
  );
};

export default Title;
