
// Recebe uma string numérica com ponto e vírgula e inverte virgula por ponto (padrão brasileiro)
export const InvertCommaAndPeriod = (value: string) => {
    const placeholder = '_XYZ_'
    let newValue = value.replaceAll(',', placeholder)
    newValue = newValue.replaceAll('.', ',')
    newValue = newValue.replaceAll(placeholder, '.')
    return newValue
}

// Mesma coisa acima só que recebendo um valor numérico
export const InvertCommaAndPeriodNumber = (value: number) => {
    return InvertCommaAndPeriod(value.toLocaleString('en-US', {minimumFractionDigits: 2}))
}